:root {
    --white: #fff;
    --black: #000;
    --highlight: #f1cdb3;
    --gray: #545454;
    --background_light: #fafafa;
    --background_dark: #f6f6f6;
    --light_gray: #b2b2b2;
}a {
    text-decoration: none;
}
a:focus {
    color: var(--light_gray); 
}
a:visited {
    color: var(--light_gray);
}:root {
    font-size: 16px;
}
h1 {
    font-size: 2.75em;
