    font-weight: 400;
}
h2 {
    font-size: 2em;
    color: var(--gray);
    line-height: 2.8rem;
    font-weight: 400;
}button {
    width: 205px;
    height: 50px;
    border: 3px solid var(--highlight);
    border-radius: 36px;
    background-color: transparent;
    outline: none;
    cursor: pointer;
    font-family: 'Georgia', serif;
}

button:hover {
    background-color: var(--highlight);
}


































